.program{
  margin-top: 22px;
}
.program__list{
  list-style: none;
  padding: 0;
  margin: 0;
}
.program__item{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  background: #F2F2F2;
  margin-left: 20px;
  margin-right: 20px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 18px;
  margin-bottom: 20px;
  max-height: 266px;
}
.program__image{
  margin-right: 20px;
}
.program__name{
  position: relative;
  .font(normal;);
  text-transform: uppercase;
  font-size: 24px;
  line-height: 37px;
  margin: 0;
}
.program__description{
  font-size: 14px;
  line-height: 18px;
  max-width: 240px;
  min-height: 112px;
  border-bottom: 1px solid #D9D9D9;
  margin: 0;
  margin-top: 22px;
  margin-bottom: 19px;
  color: @special-dark-grey;
}
.program__link{
  position: relative;
  text-transform: uppercase;
  color: @black;
  text-decoration: none;
  font-size: 16px;
  line-height: 16px;
  &::before{
    content: "";
    position: absolute;
    background-image: url(../img/arrow-program.svg);
    background-repeat: no-repeat;
    width: 24px;
    height: 12px;
    top: 2px;
    right: -40px;
  }
}
